.carruselContainer {
    border: 1px solid red;
    align-items: center;
    background: #ffff;
    display: flex;
    height: 60vh;
    justify-content: center;
  }



@mixin white-gradient {
    border: 1px solid green;
	background: linear-gradient(to right,  rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
}

$animationSpeed: 20s;

// Animation
@keyframes scroll {    
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-35vw * 6))}
}


// Styling
.slider {
	background: white;
	box-shadow: 0 10px 20px -5px rgba(0, 0, 0, .125);
	height: 40vh;
	margin: auto;
	overflow:hidden;
	position: relative;
	width: 100vw;
	
	&::before,
	&::after {
		@include white-gradient;
		content: "";
		height: 100px;
		position: absolute;
        border: 1px solid yellow;
		width: 10px;
		z-index: 2;
	}
	
	&::after {
		right: 0;
		top: 0;
		transform: rotateZ(180deg);
	}

	&::before {
		left: 0;
		top: 0;
	}
	
	.slide-track {
		animation: scroll $animationSpeed linear infinite;
		display: flex;
		width: calc(35vw * 12);
	}
	
	.slide {
        display: flex;
        justify-content: center;
        border: 1px solid red;
		height: 40vh;
		width: 35vw;
	}
    #team1, #team2, #team3, #team4,#team5,#team6{
        background-position: center;
        background-repeat: no-repeat;
        background-size: 80%;  
       
    }

    #team1{       
        border: 1px solid red;    
        display: flex; 
        min-height: 40vh;
        min-width: 35vw;
        max-height: 40vh;
        max-width: 35vw;
        transition-duration: 1s;
        .img1_1{
            position: absolute;
            border: 1px solid red;
            visibility: visible;
            min-height: 40vh;
            min-width: 35vw;
            max-height: 40vh;
            max-width: 35vw;
            background-position: center;
            background-repeat: no-repeat;
            background-size: 80%;  
            background-image: url('../../img/pagefive/Ari/Ari_001.jpg');   
        }
        .img1_2{
            visibility: hidden;
        }
    }
    #team1:hover{
        .img1_1{
            visibility: hidden;
        }
        .img1_2{
            border: 1px solid blue;
            visibility: visible;
            min-height: 40vh;
            min-width: 35vw;
            max-height: 40vh;
            max-width: 35vw;
            background-position: center;
