@media (max-width: 1000px) and (orientation: landscape) {



        .containerContentPageFive{
          min-width: 100vw;
          max-width: 100vw;
          min-height: 130vh;
          display: flex;
          justify-content: center;
          align-items: center;
      }
      .containerContentPageFiveContentPrincipal{
      }
      .containerContentPageFiveContentPrincipalHeader{
        .textOne{
          font-size: 1.5rem;
        }
        .textTwo{
          line-height: 4.5rem;
          font-size: 5.5rem;
        }
      }
    
      .containerContentPageFiveContentPrincipalCentral{
          min-height: 70vh;
      }
    
      .containerContentPageFiveContentPrincipalFooter{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-top: 15vh;
        .textOne{
          text-align: center;
          width: 100%;
          display: flex;
          justify-content: flex-end;
          font-size: 1.8rem;
        }
        .textTwo{
          display: flex;
          justify-content: flex-start;
          font-size: 4.2rem;
          font-weight: 500;
          width: 100%;
          color: white; 
          text-shadow: white 0em 0em 0em;
          margin: 0;
          .imagen{
            margin: 0;
            max-width: 70%;
         }
        }
      }
    







