	.sliderFive {
		


		@keyframes scrollFive {
			0% { transform: translateX(0); }
			100% { transform: translateX(calc(-40vw * 8))}
		}
		
		
		.slide-trackFive {
			width: calc(40vw * 16);
		}
		.slide-trackFive:hover {
