#navbar{
    font-family: 'open sans';
    width: 100%;
   
    display: flex;
    align-items: center;
    position: fixed;
    top:0;
}

    /***************       NORMAL    ********************/
    /*     HEADER CLOSE     */
    
    .HeaderNavContainer, .HeaderNavContainerWhite, .HeaderNavContainerBlack{
        position: absolute;
        z-index: 20;
        min-width: 100vw;
        max-width: 100vw;
        min-height: 8vh;
        .navigationMenu{
            min-width: 100vw;
            max-width: 100vw;
            .MenuBottoms{
                display: flex;
                flex-direction: row;
                .containerTBE{
                    cursor: pointer;
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    background-color: #FF4700;
                    min-height: 8vh;
                    min-width: 7vw;
                    .img{
                       
                        min-width: 4vw;
                        max-width: 4vw;
                    }
                }
                .containerLinks{
                    flex-grow: 3;
                    display: flex;
                    justify-content: flex-end;                
                    align-items: center;
                    background-color: #FF4700;
                    padding-right: 5vw;
                    .imagen{
                        margin-left: 2vw;
                        width: 2vw;
                        img{
                            min-width: 75%;
                        }
                    }
                    .imagenMovil{
                        display: none;
                    }
                }
                .containerBotton{
                    background-color: #FF4700;
                    margin: 0;
                    padding: 0;
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    min-width: 5vw;
                    color: #FFF;
                }
            }
        }


        .MenuTitleHeaders{

            background-color: #FF4700;
            display: flex;
            flex-direction: row;
            min-height:  0vh;
            max-height:  0vh;
            animation: ocultarMenu 0.8s;
            div{
                width: 30vw;
            }
            .elementThree {
                
                min-height: 0vh;
                max-height: 0vh;
                flex-grow: 2;
                display: flex;
                flex-direction: column;
                ul{
                    margin: 0;
                    padding: 0;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-start;
                    width: 100%;
                    min-height: 80%;
                    animation: ocultarTitleClose 0.8s;
                    li{
                        cursor: pointer;
                        width: 60%;
                        display: flex;
                        justify-content: flex-start;
                        align-items: center;
                        
                        border: none;
                        list-style: none;
                        font-size: 3rem;
                        font-family: Montserrat;
                        min-height: 25%;
                        font-weight: 600;
                       
                        background-size:    100% 100%;//,
                        background-position:   0% 90%;                
                    }

                    li {
                        margin: 0;
                        padding: 0;
                        background-repeat: no-repeat;
                        background-image:   linear-gradient(to right, #C9C9C9 100%, #C9C9C9 100%);
                        background-size:    100% .5%;//,
                        background-position:   0% 99%;      
                        
                        font-size: 0rem;
                        .Links{
                            font-size: 0rem;
                            animation: desaparecerTexto 0.5s;
                        }
                        @keyframes desaparecerTexto {
                            0%{
                                font-size: 2.5rem;
                            }
                            10%{
                                background-color: none;
                                color: transparent;
                            }
                            100%{
                                color: transparent;
                                background-color: none;
                                font-size: 2.5rem;
                            }                            
                        }
                    }
                    
                    li:hover {
                        .Links{
                            color:#000;
                        }
                    }

                    .Links{
                        margin: 0;
                        padding: 0;
                        background-repeat: no-repeat;
                        background-image:   linear-gradient(to right, #C9C9C9 100%, #C9C9C9 100%);
                        background-size:    100% .5%;//,
                        background-position:   0% 99%;          
                        min-height: 100%;
                        min-width: 100%;
                        display: flex;
                        align-items: center;
                    }
                }    
                
                
                div{
                    display: flex;
                    justify-content: flex-start;                  
                    .imagen{
                        color: #000;
                        width: 6%;
                        margin-right: 10%;
                    }                   
                    
                }
                
            }                   
        }       

    }
    .HeaderNavContainerWhite{      
        .navigationMenu{     
            .MenuBottoms{            
                .containerTBE{                  
                    background-color: #FFF;               
                }
                .containerLinks{
                    background-color: #FFF;                 
                }
                .containerBotton{
                    background-color: #FFF;                   
                }
            }
        }

    }
    .HeaderNavContainerBlack{      
        .navigationMenu{     
            .MenuBottoms{            
                .containerTBE{                  
                    background-color: #000;               
                }
                .containerLinks{
                    background-color: #000;                 
                }
                .containerBotton{
                    background-color: #000;                   
                }
            } 
        }

    }


