                        background-size:    100% .5%;//,
                        background-position:   0% 99%;        
                        .Links{
                            animation: aparecerTitleHeader 0.8s;  
                        }
                          
                    }
                    
                    li:hover {
                        animation: TitleHov 1.25s;
                        color:yellow;
                        .Links{
                            color:#000;
                        }
                    }

                    .Links{
                        margin: 0;
                        padding: 0;
                        background-repeat: no-repeat;
                        background-image:   linear-gradient(to right, #C9C9C9 100%, #C9C9C9 100%);
                        background-size:    100% .5%;//,
                        background-position:   0% 99%;          
                        min-height: 100%;
                        min-width: 100%;
                        display: flex;
                        align-items: center;
                    }
                    
                }    
                
                
                div{
                    display: flex;
                    justify-content: flex-start;                  
                    .imagen{
                        width: 6%;
                        margin-right: 10%;
                        img{
                            min-width: 150%;
                        }
                    }                   
                    .imagenMovil{
                        display: none;
                    }
                }
                
            }                   
        }        
    }
    @keyframes aparecerTitleHeader{
        0% {
            display: none;
            color: transparent;
        }
        70%{
            color: transparent;
        }
        100% {
            display: flex;
           
        }
    }
    @keyframes ocultarTitleClose{
        0% {
            display: flex;
            min-height:  30vh;
            max-height:  30vh;
        }
        
        100% {
            display: none;
            min-height:  0vh;
            max-height:  0vh;
        }
    }
    @keyframes ocultarMenu {
        0% {
            display: flex;
            min-height:  40vh;
            max-height:  40vh;
        }
        
        100% {
            display: flex;
            min-height:  0vh;
            max-height:  0vh;
        }
    }

    @keyframes TitleHov {
        0% {
            display: none;
            color: transparent;
            background-size:  0% 1.5px;
        }             
        90%{
            display: none;
            color: transparent;
        }                          
        100% {
            display: flex;
            color: transparent;
            background-size:    100% 1.5px;     
        }
    }
    @keyframes MenuContener {
        0% {
            min-height:  0vh;
            max-height:  0vh;
        }
        
        100% {
            min-height:  40vh;
            max-height:  40vh;
        }
    }
    
    @keyframes AparecerLogos {
        0% {
           margin-bottom: -10vh;
        }
        100% {
        margin-bottom: 0vh;
        }
    }





