.containerContentPageTwo{
    margin: 0;
    min-width: 100vw;
    min-height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: opacity(0.1s);   
    div{
        background-color: transparent;
    } 
    .containerSubContentPageTwo{
    }
}

.titlePageTwo{
    width: 90vw;
    min-height: 20vh;
    max-height: 20vh;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #FFF;
    font-family:  Montserrat;
    font-size: 1rem;
    img{
        margin-right: 1%;
    }
}
.containerSubContentPageTwo{
    min-width:  90vw;
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    .subtitleContainerPageTwo{
        min-width: 55vw;
        max-width: 55vw;
        text-align: left;
        color: #FFF;
        font-family:  Montserrat;       
        font-size: 3rem;
        font-stretch: condensed;
       
    }
}
.optionSelectSubtitle{
    margin-top: 1%;
    min-width:  85vw;
    display: flex;
    flex-direction: row;
    .submenuPageTwo{
        text-align: left;
        min-width: 55%;
        height: 40vh;
        display: flex;        
        align-items: flex-start;
        font-family:  Montserrat;
        .submenu{
            
            width: 80%;
            list-style: none;
            color: #707070;
            font-size: 2.3rem;
            font-weight: 800;
            font-family:  Montserrat;
            li{
                cursor: pointer;
                display: flex;
                align-items: center;
                margin-left:10%;
                width: 90%;
                min-height: 5.5vh;
                //border-bottom: 1px solid #848484;
                min-height: 5vh;

                text{
                    width: 80%;
                    min-height: 5vh;
                    border-bottom: 0.5px solid rgba(255,255,255, 0.2);
                }

                
                background-image:   linear-gradient(to right, #C9C9C9 100%, #C9C9C9 100%);
                background-size:    100% 100%;//,
                background-position:   0% 99%;

                background-repeat: no-repeat;
                background-image:   linear-gradient(to right, #C9C9C9 100%, #C9C9C9 100%);
                background-size:    80% 1%;//,
                background-position:   0% 99%;          
            }

            li:hover{
                animation: TitleHover 1.25s cubic-bezier(0.19, 1, 0.22, 1) 1;
            }
            @keyframes TitleHover {
                0% {
                    background-size:  0% 1.5px,
                }                                       
                100% {
                    background-size:    80% 1.5px,                                        
                }
            }
            .suboptionSelected{
                color: #FA500F;
                //border-bottom: 1px solid #FFFFFF;
                //border-bottom: 1px solid rgba(255,255,255, .8);
            }
        }
    }
    .contentMenuPageTwo{
        min-width: 25%;
        max-width: 25%;
        display: flex;
        padding-top: 5vh;
        .textContainer{
                .title{
                    color: #FFF;
                    font-size: 1.4rem;
                    font-family: Montserrat;
                    margin-bottom: 3%;
                }
                .subtitle{
                    color: #FA500F;
                    font-size: 1.2rem;
                    font-family: Montserrat;
                    margin-bottom: 3%;
                }
                .text{                    
                    max-width: 25vw;
                    color: #CBCBCB;
                    font-size: 1rem;
                    font-weight: 400;
                    text-align: justify;
                    font-family: Montserrat;
                   // line-height: 150%;
                }
                .Links{
                    color: #FFF;
                    display: flex;
                    justify-content: flex-end;
                    align-items: flex-end;
                    min-height: 10vh;
                    font-size: 1.3rem;
                    font-family: Montserrat;
                    width: 100%;
                  //  border: 1px solid yellow;
                    border-bottom: 0.2px solid rgba(255,255,255, .3);
                    .boton{
                        display: flex;
                        width: 100%;
                        min-height: 4vh;
                        margin-bottom: 0;
                        text{
                            //: 1px solid red;
                            width: 95%;
                            font-size: 1.2rem;
                            font-weight: 400;
                           // border-bottom: 1px solid rgba(255,255,255, .3);
                        }
                    }
                    background-image:   linear-gradient(to right, #C9C9C9 100%, #C9C9C9 100%);
                    background-size:    90% 100%;//,
                    background-position:   0% 100%;
    
                    background-repeat: no-repeat;
                    background-image:   linear-gradient(to right, #C9C9C9 100%, #C9C9C9 100%);
                    background-size:    100% 0.5%;//,
                    background-position:   0% 100%;          
                }
                .Links:hover{                  
                    animation: TitleHoverSubPageTwo 1.25s cubic-bezier(0.19, 1, 0.22, 1) 1;            
                }
                @keyframes TitleHoverSubPageTwo {
                    0% {
                        background-size:  0% 1.5px;
                        color: #FFF;
                    }                                       
                    100% {
                        background-size:    100% 1.5px;
                        color: #FA500F;
                    }
                }
        }
       
    }
}

.Links{
    text-decoration: none;
    
    padding-bottom: 10px;
}

.textContainerDesplegado{
    display: none;
}


.optionSelectSubtitleMovil{
    display: none;
}














@media (max-width: 700px) {

    .containerContentPageTwo{
    min-height: 70vh;
    margin-bottom: 10vh;
    }
    
    .titlePageTwo{        
        max-width: 90vw;
        max-height: 15vh;
        min-height: 15vh;
        img{
            max-width: 3vw;
            margin-right: 2vw;
        }
    }
    .containerSubContentPageTwo{
        display: flex;
        align-items: center;
        .subtitleContainerPageTwo{
            font-size: 2rem;
            text-align: left;
            max-width: 90vw;
            color: #FFF;
            margin-bottom: 10vw;
            h1{
             font-family: Montserrat;
            }
        }
    }
    
  
    .optionSelectSubtitle{
       display: none;
    }



    .optionSelectSubtitleMovil{
        width: 90vw;
        display: flex;
        color: #FFF;
        margin: 0;
        padding: 0;
      
        ul{
            width: 90vw;
            margin: 0;
            padding: 0;
           
            .opcionesDesplegadas{
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                padding-bottom: 6vh;
                text{
                    max-width: 80vw;
                    min-width: 80vw;
                    color: #FA500F;
                }
                .img{
                    min-width: 9vw;
                    max-width: 9vw;
                    img{
                        position: absolute;
                        width: 20px;
                        height: 20px;
                    }
                }
                .textContainerDesplegado{
                    display: flex;
                    color: #FFF;
                    flex-direction: column;
                    div{
                        margin-bottom: 1vh;
                    }
                    .titleDesplegado{
                        font-family: Montserrat;
                        font-weight: 300;
                        font-size: 1rem;
                        min-height: 2vh;
                        max-height: 2vh;
                        animation: crecer 1s;
                    }
                    .subtitleDesplegado{
                        color: #FA500F;
                        font-family: Montserrat;
                        font-weight: 400;
                        font-size: 1rem;
                        min-height: 2vh;
                        max-height: 2vh;
                        animation: crecerDos 1.5s;
                    }
                    .textDesplegado{
                        font-size: 1rem;
                        min-width: 90%;
                        max-width: 90%;
                        text-align: justify;
                        color: #CBCBCB;
                        min-height: 6vh;
                        max-height: 6vh;
                        font-family: HelveticaNeue;
                        animation: crecerTres 0.9s;
                    }


                    @keyframes crecer {
                        0%{
                            color: transparent;
                            min-height: 0vh;
                            max-height: 0vh;
                        }
                        80%{
                            color: transparent;
                        }
                        100%{
                            color: #fff;
                            min-height: 2vh;
                            max-height: 2vh;
                        }
                    }
                    @keyframes crecerDos {
                        0%{
                            color: transparent;
                            min-height: 0vh;
                            max-height: 0vh;
                        }
                        50%{
                            color: transparent;
                        }
                        100%{
                            color: #FA500F;
                            min-height: 2vh;
                            max-height: 2vh;
                        }
                    }
                    @keyframes crecerTres {
                        0%{
                            color: transparent;
                            min-height: 0vh;
                            max-height: 0vh;
                        }
                        90%{
                            color: transparent;
                        }
                        100%{
                            color: #C9C9C9;
                            min-height: 6vh;
                            max-height: 6vh;
                        }
                    }
                }            
            }




            .opcionesEscondido{
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                text{
                    max-width: 80vw;
                    min-width: 80vw;
                    color: #FFF;
                }
                .img{
                    min-width: 9vw;
                    max-width: 9vw;
                    img{
                        position: absolute;
                        width: 20px;
                        height: 20px;
                    }
                }
                .textContainerEscondido{
                    display: flex;
                    color: #FFF;
                    flex-direction: column;
                    margin-bottom: 1vh;
                    div{
                    }
                    .titleEscondido{
                        font-family: Montserrat;
                        font-weight: 300;
                        font-size: 1rem;
                        min-height: 0vh;
                        max-height: 0vh;
                        color: transparent;
                        animation: esconder 1s;
                    }
                    .subtitleEscondido{
                        color: transparent;
                        font-family: Montserrat;
                        font-weight: 400;
                        font-size: 1rem;
                        min-height: 0vh;
                        max-height: 0vh;
                        animation: esconderDos 1s;
                    }
                    .textEscondido{
                        font-size: 1rem;
                        min-width: 90%;
                        max-width: 90%;
                        text-align: justify;
                        color: transparent; 
                        min-height: 0vh;
                        max-height: 0vh;
                        font-family: HelveticaNeue;
                        animation: esconderTres 0.9s;
                    }


                    @keyframes esconder {
                        0%{
                            color: #fff;
                            min-height: 2vh;
                            max-height: 2vh;
                        }
                        20%{
                            color: transparent;
                        }
                        100%{                            
                            color: transparent;
                            min-height: 0vh;
                            max-height: 0vh;
                        }
                    }
                    @keyframes esconderDos {
                        0%{
                            color: #FA500F;
                            min-height: 2vh;
                            max-height: 2vh;
                        }
                        40%{
                            color: transparent;
                        }
                        100%{                          
                            color: transparent;
                            min-height: 0vh;
                            max-height: 0vh;
                        }
                    }
                    @keyframes esconderTres {
                        0%{
                            color: #C9C9C9;
                            min-height: 6vh;
                            max-height: 6vh;
                        }
                        10%{
                            color: transparent;
                        }
                        100%{
                           
                            color: transparent;
                            min-height: 0vh;
                            max-height: 0vh;
                        }
                    }
                }            
            }











            li{
                cursor: pointer;
                width: 90vw;
                list-style: none;
                display: flex;
                border-bottom:1px solid rgba(255,255,255, .3);
                text{
                    min-width: 80vw;
                    line-height: 3rem;
                    font-size: 1.2rem;
                    font-family: Montserrat;                                        
                }
                .img{
                    min-width: 10vw;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    img{
                        position: absolute;
                    }
                }



                .textContainerDesplegado{
                    
                }


            }
           


        }
        
    }
}