.principalContainer{
    min-height: 500vh;
    background-color: #000;
    .ContainerPaginaUno{
        position: fixed;
        margin-top: 0vh;
        margin-left: 0vw;
        z-index: 19;
        min-height: 100vh;
        background-color: #FA500F;
    }
    .ContainerPaginaDos{
        z-index: 5;
        min-height: 100vh;
        display: none;
    }
    .ContainerPaginaTres{
        z-index: 5;
        min-height: 100vh;
        display: none;
    }
    .ContainerPaginaCuatro{
        z-index: 5;
        min-height: 100vh;
        display: none;
    }


    .containerSectionOne{
        position: fixed;
        margin-top: 60vh;
        margin-left: 50vw;
        min-width: 40vw;
        max-width: 40vw;
        z-index: 20;
        min-height: 40vh;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font-size: 10vw;
          
        text{
            font-size: 3vw;
            text-align: left;
            margin-right: 0vw;
            color: transparent; 
            font-family: Montserrat;
        }
    }

    .containerSectionTwo{
        position: fixed;
        margin-top: 60vh;
        margin-left: 60vw;
        min-width: 30vw;
        max-width: 30vw;
        z-index: 20;
        min-height: 40vh;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font-size: 10vw;
        text{
            font-family: Montserrat;
            font-size: 3vw;
            text-align: left;
            margin-right: 0vw;
            color: transparent; 
        }
    }
}




@media (max-width: 700px) {
    .principalContainer{
        min-height: 500vh;
        background-color: #000;
        .ContainerPaginaUno{
            position: fixed;
            margin-top: 0vh;
            margin-left: 0vw;
            z-index: 19;
            min-height: 100vh;
            background-color: #FA500F;
        }
        .ContainerPaginaDos{
            z-index: 5;
            min-height: 100vh;
            display: none;
        }
        .ContainerPaginaTres{
            z-index: 5;
            min-height: 100vh;
            display: none;
        }
        .ContainerPaginaCuatro{
            z-index: 5;
            min-height: 100vh;
            display: none;
        }
    
    
        .containerSectionOne{
            position: fixed;
            margin-top: 60vh;
            margin-left: 30vw;
            min-width: 60vw;
            max-width: 60vw;
            z-index: 20;
            min-height: 40vh;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            font-size: 10vw;
              
            text{
            
                font-size: 8vw;
                text-align: left;
                margin-right: 0vw;
                color: transparent; 
            }
        }
    
        .containerSectionTwo{
            position: fixed;
            margin-top: 60vh;
            margin-left: 40vw;
            min-width: 50vw;
            max-width: 50vw;
            z-index: 20;
            min-height: 40vh;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            font-size: 10vw;
            text{
                font-size: 8vw;
                text-align: left;
                margin-right: 0vw;
                color: transparent; 
            }
        }
    }    
}