@mixin white-gradient {
	background: linear-gradient(to right,  rgba(255,255,255,0) 0%,rgba(255,255,255,0) 20%);
}



$animationSpeedFive: 20s;

// Animation
@keyframes scrollFive {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-35vw * 8))}
}


// Styling
.sliderFive {

	
	background-color: none;
	min-width: 100vw;
	max-width: 100vw;
	
	min-height: 60vh;
	max-height: 60vh;
	overflow:hidden;
	overflow-x: hidden;
	display: flex;
	justify-content: flex-start;
	align-items: flex-end;
	&::before,
	&::after {
		@include white-gradient;
		content: "";
		height: 100%;
		width: 10px;
		z-index: 2;
	}
	
	&::after {
		right: 0;
		top: 0;
		transform: rotateZ(180deg);
	}

	&::before {
		left: 0;
		top: 0;
	}
	
	.slide-trackFive {
		animation: scrollFive $animationSpeedFive linear infinite;
		display: flex;
		width: calc(35vw * 16);
	}
	.slide-trackFive:hover {
	//	animation-play-state: paused;
	}
	
	.slideFive {

		min-height: 60vh;
		min-width: 35vw;
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		
		div{
			min-width: 80%;
			min-height: 100%;
			background-size: 100%;
			background-repeat: no-repeat;
		}

		.Foto1{
