			.textNameTeam{	
				margin-right: -20%;
				margin-top: 20%;			
				min-width: 50%;
				display: flex;
				flex-direction: column;
				font-family: Montserrat;
			
				.Name{
					color: #FF4700;;
					text{
						color: white;
					}
					margin-bottom: 15px;
				}
				.position{
