    @media (max-width: 1000px) and (orientation: landscape) {

        .Texto1{
            display: none;
        }
        .Texto2{
            display: none;
        }

        .containerContentPageOne{
            display: flex;
            justify-content: center;
            align-items: center;
            min-height: 90vh;
            max-height: 100vh;
            padding-top: 10vh;
            .contentPageOne{
                display: flex;
                justify-content: center;
                padding: 0;
                margin: 0;
                .oneContentPageOne{
                    padding: 0;
                    min-height: 60vh;
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    img{
                        padding: 0;
                        min-width: 40%;
                    }
                    .twoContentPageOne{
                        max-width: 0%;
                        display: none;
                    }
                    
                }
                .threeContentPageOne{
                    padding-top: 10%;
                    min-width: 85vw;
                    max-width: 85vw;
                    .slideHorizontal{
                        min-width: 85vw;
                        max-width: 85vw;
                        .subContentOnePageOne{
                            width: 80%;
                            margin-left: 0%;
                            font-size: 1.5rem;
                        }
                        .subContentTwoPageOne{
                            width: 80%;
                            margin-left: 20%;
                            font-size: 1.5rem;
                        }
                    }
                }
            }            
        }            
    }